import React from 'react'
import Code, { defaultProps } from 'prism-react-renderer'
import theme from 'prism-react-renderer/themes/vsLight'
import CodeBlock from './CodeBlock'
import './index.css'

function HighlightCode({ code, language }) {
  return (
    <Code {...defaultProps} code={code} language={language} theme={theme}>
      {({ tokens, getLineProps, getTokenProps }) => (
        <pre className="code-editor-highlight">
          {tokens.map((line, i) => (
            <div
              key={`${i.toString()}`}
              {...getLineProps({
                line,
                key: i
              })}
            >
              {line.map((token, key) => (
                <span
                  key={`${key.toString()}`}
                  {...getTokenProps({
                    token,
                    key
                  })}
                />
              ))}
            </div>
          ))}
        </pre>
      )}
    </Code>
  )
}

export default function Highlight({ code, language, ...rest }) {
  return (
    <CodeBlock
      {...rest}
      code={code}
      component={<HighlightCode code={code} language={language} />}
      language={language}
    />
  )
}
