import React from 'react'
import Live from './Live'
import Preview from './Preview'
import Highlight from './Highlight'

export default (props) => {
  const { children, live, render, scope, version } = props
  const { children: source, className } = children.props
  const code = source?.trim()
  const language = className.replace(/language-/, '')
  let content
  if (live) {
    content = <Live scope={scope} code={code} language={language} version={version} />
  } else if (render) {
    content = <Preview scope={scope} code={code} version={version} />
  } else {
    content = <Highlight code={code} language={language} version={version} />
  }
  return (
    <div className="relative">
      <div className="mx-1 mt-2 mb-4 border border-solid rounded-md border-slate-200 shadow-sm overflow-hidden code-block">
        {content}
      </div>
    </div>
  )
}
