// import React from 'react'

import { getHeadingId } from '@/util/hash'

export function loop(list, _maxDepth = 4) {
  const tree = []
  const cache = {}
  const tocIds = []
  let currentParent
  // 使用 h2，h3，h4（可选）
  const maxDepth = Math.max(2, Math.min(_maxDepth, 4))
  list
    .map((item) => {
      item.hashId = getHeadingId(item.text, cache)
      return item
    })
    .filter(({ depth }) => depth > 1 && depth <= maxDepth)
    .forEach(({ depth, text, hashId }) => {
      tocIds.push(hashId)
      if (depth === 2 || !currentParent) {
        if (depth === 2) {
          currentParent = {
            text,
            hashId,
            children: []
          }
        } else {
          currentParent = {
            children: []
          }
        }
        tree.push(currentParent)
      }
      if (depth > 2) {
        currentParent.children.push({
          depth,
          text,
          hashId
        })
      }
    })
  return [tree, tocIds]
}

export function getScrollElement(tocIds = []) {
  let targetId
  for (let i = 0; i < tocIds.length; i++) {
    const id = tocIds[i]
    const el = document.getElementById(id)
    const { top } = el.getBoundingClientRect()
    if (top >= 80) {
      targetId = targetId || id
      break
    } else {
      targetId = id
    }
  }

  return targetId
}
