import React, { useState, useEffect } from 'react'
import { Table } from 'antd'

export default function ApiTable({ data }) {
  const [columns, setColumns] = useState([])
  const [dataSource, setDataSource] = useState([])

  useEffect(() => {
    const header = [{
      title: <span className="select-none">参数</span>,
      dataIndex: 'name',
      sorter: (a, b) => a.name[0]?.charCodeAt() - b.name[0]?.charCodeAt()
    }, {
      title: <span className="select-none">类型</span>,
      dataIndex: 'type'
    }, {
      title: <span className="select-none">默认值</span>,
      dataIndex: 'defaultValue'
    }, {
      title: <span className="select-none">说明</span>,
      dataIndex: 'description'
    }]
    const showVersion = false
    const list = data.map(([name, type, defaultValue, description, version], index) => {
      if (!showVersion && version?.length > 0) {
        columns.push({
          title: '版本',
          dataIndex: 'version'
        })
      }
      return {
        key: `${index.toString()}`,
        name,
        type,
        defaultValue,
        description,
        version
      }
    })
    setColumns(header)
    setDataSource(list)
  }, [])
  if (columns.length) {
    return (
      <div className="mb-2">
        <Table
          columns={columns}
          dataSource={dataSource}
          pagination={false}
          size="small"
          bordered
        />
      </div>
    )
  }
  return <></>
}
