import React, { useState, useEffect } from 'react'
import { useLocation } from 'react-router-dom'
import CodeBlock from '@/component/markdown/code'
import components from '@/component/markdown/components'
import headings from '@/component/markdown/heading'
import { scrollIntoView } from '@/util/hash'
import clsx from 'clsx'
import { Helmet } from 'react-helmet'
import Sidebar from './sidebar'
import Toc from './toc'

export default function Doc() {
  const [doc, setDoc] = useState(null)
  const { pathname, hash } = useLocation()
  const path = pathname.replace('/doc/', '')

  useEffect(() => {
    async function load() {
      const mod = await import(
        /* webpackInclude: /\.mdx$/ */
        /* webpackChunkName: "doc-[request]" */
        `../../../doc/${path}.mdx`
      )
      setDoc(mod)
      if (hash) {
        const id = decodeURIComponent(hash.replace('#', ''))
        setTimeout(() => {
          scrollIntoView(document.getElementById(id))
        }, 64)
      }
    }
    load()
  }, [])

  let Document
  let injectComponents
  let sidebar = true
  let toc = true
  let expand = false
  if (doc) {
    const scope = Object.keys(doc).reduce((cache, key) => {
      const v = doc[key]
      if (key === 'default') {
        Document = v
      } else if (typeof v === 'object' || typeof v === 'function') {
        cache[key] = v
      } else if (key === 'sidebar') {
        sidebar = v
      } else if (key === 'toc') {
        toc = v
      } else if (key === 'expand') {
        expand = v
      }
      return cache
    }, {})
    if (toc) {
      expand = false
    }
    injectComponents = {
      ...components,
      ...headings(),
      pre: (props) => <CodeBlock {...props} scope={scope} />
    }
  }

  return (
    <>
      {
        Document
          ? (
            <div className={clsx(
              (sidebar || toc) ? 'max-w-[90rem]' : 'max-w-[52rem]', 'mx-auto px-1 pt-1'
            )}
            >

              <div className="hidden lg:block fixed z-20 right-auto w-[17.5rem] h-full pl-4 pr-10 pb-12 mb-2 overflow-y-auto">
                {sidebar === false ? undefined : <Sidebar />}
              </div>
              <div className={clsx({
                'lg:pl-[17.5rem]': sidebar || toc
              })}
              >
                <div className={clsx(
                  // eslint-disable-next-line no-nested-ternary
                  toc ? 'xl:mr-[15rem] lg:pr-10' : (sidebar && expand ? 'lg:pr-5' : 'max-w-[52rem]'),
                  'mx-auto xl:ml-0'
                )}
                >
                  <div className="relative z-20 pt-1 mt-2">
                    {(doc.title || doc.date || doc.tags)
                      ? (
                        <div className="pb-1.5 mb-7 border-0 border-b border-dashed border-slate-200">
                          {
                            doc.title
                              ? (
                                <>
                                  <Helmet>
                                    <title>{doc.title}</title>
                                  </Helmet>
                                  <h1 className="text-3xl text-slate-900">{doc.title}</h1>
                                </>
                              )
                              : undefined
                          }
                          <div className="relative overflow-hidden h-4 text-gray-400 text-xs pl-1 pr-2">
                            {
                              doc.tags?.map((text) => <span key={text} className="mr-1 px-1">{text}</span>)
                            }
                            <span className="absolute right-3">
                              {
                                doc.wordCount >= 0 ? (
                                  <span className="mr-4">
                                    约
                                    <span>{doc.wordCount}</span>
                                    字
                                  </span>
                                )
                                  : undefined
                              }
                              {doc.date ? <span>{doc.date}</span> : undefined}
                            </span>
                          </div>
                        </div>
                      )
                      : undefined}

                    <Document components={injectComponents} />
                  </div>
                  {
                    doc.toc === false
                      ? undefined
                      : (
                        <div className="fixed z-20 top-1 bottom-1 right-[max(0px,calc(50%-45rem))] w-[17.5rem] py-10 px-8 overflow-y-auto hidden xl:block">
                          <Toc list={doc.tocData} />
                        </div>
                      )
                  }
                </div>
              </div>
            </div>
          )
          : <div>loading</div>
      }
    </>
  )
}
