import React from 'react'
import { getHeadingId } from '@/util/hash'
import HeadingChild from './HeadingChild'

function H1({ text, depth, hashId }) {
  return <h1 className="group text-3xl text-slate-900" id={hashId}><HeadingChild text={text} hashId={hashId} depth={depth} /></h1>
}
function H2({ text, depth, hashId }) {
  return <h2 className="group text-xl -ml-4 pl-4" id={hashId}><HeadingChild text={text} hashId={hashId} depth={depth} /></h2>
}
function H3({ text, depth, hashId }) {
  return <h3 className="group text-base -ml-4 pl-4" id={hashId}><HeadingChild text={text} hashId={hashId} depth={depth} /></h3>
}
function H4({ text, depth, hashId }) {
  return <h4 className="group text-sm -ml-4 pl-4" id={hashId}><HeadingChild text={text} hashId={hashId} depth={depth} /></h4>
}
function H5({ text, depth, hashId }) {
  return <h5 className="group -ml-4 pl-4" id={hashId}><HeadingChild text={text} hashId={hashId} depth={depth} /></h5>
}
function H6({ text, depth, hashId }) {
  return <h6 className="group -ml-4 pl-4" id={hashId}><HeadingChild text={text} hashId={hashId} depth={depth} /></h6>
}

export default function headings() {
  const cache = {}

  return {
    h1: ({ children: text }) => <H1 text={text} hashId={getHeadingId(text, cache)} depth={1} />,
    h2: ({ children: text }) => <H2 text={text} hashId={getHeadingId(text, cache)} depth={2} />,
    h3: ({ children: text }) => <H3 text={text} hashId={getHeadingId(text, cache)} depth={3} />,
    h4: ({ children: text }) => <H4 text={text} hashId={getHeadingId(text, cache)} depth={4} />,
    h5: ({ children: text }) => <H5 text={text} hashId={getHeadingId(text, cache)} depth={5} />,
    h6: ({ children: text }) => <H6 text={text} hashId={getHeadingId(text, cache)} depth={6} />
  }
}
