import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { RightOutlined } from '@ant-design/icons'
import { debounce } from 'lodash'
import { hashchange } from '@/util/hash'
import { getScrollElement, loop } from './util'

export default function Toc({ list, maxDepth = 4 }) {
  const history = useHistory()
  const [hash, setHash] = useState(decodeURI(history.location.hash?.replace(/^#/, '')))
  const [data, setData] = useState([])

  useEffect(() => {
    const [tree, tocIds] = loop(list, maxDepth)
    setData(tree)

    const listener = debounce(() => {
      const targetId = getScrollElement(tocIds)
      setHash(targetId)
    }, 100, {
      trailing: true
    })
    window.addEventListener('scroll', listener, true)
    return () => {
      window.removeEventListener('scroll', listener, true)
    }
  }, [])

  if (data.length === 0) {
    return null
  }

  return (
    <div className="markdown-toc py-6">
      <div className="font-semibold mb-1 pb-2 text-sm lg:text-xs text-gray-500 border-0 border-b border-dashed border-slate-300">本页目录</div>
      <div className="text-slate-700 text-sm leading-6">
        {data.map(({ text, hashId, children }) => (
          <div key={hashId}>
            {
              text
                ? (
                  <div>
                    <a
                      href={`#${hashId}`}
                      className={
                        `inline-block py-1 font-medium
                        ${hash === hashId ? 'text-sky-500' : 'text-slate-600 hover:text-slate-800'}`
                      }
                      onClick={(e) => {
                        hashchange(e, hashId)
                        setHash(hashId)
                      }}
                    >
                      {text}
                    </a>
                  </div>
                )
                : undefined
            }
            {
              children.map((item) => (
                <div key={item.hashId} className={item.depth === 3 ? 'pl-4' : 'pl-9'}>
                  <a
                    href={`#${item.hashId}`}
                    className={
                      `inline-flex items-center py-1
                      ${hash === item.hashId ? 'text-sky-500' : 'text-slate-500 hover:text-slate-700'} `
                    }
                    onClick={(e) => {
                      hashchange(e, item.hashId)
                      setHash(item.hashId)
                    }}
                  >
                    <RightOutlined className="mr-1 text-[0.5rem]" />
                    {item.text}
                  </a>
                </div>
              ))
            }
          </div>
        ))}
      </div>
    </div>
  )
}
