import React, { useState, useRef } from 'react'
import { RightOutlined, CopyOutlined, CheckOutlined, SmileOutlined } from '@ant-design/icons'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import { Tooltip, Tag } from 'antd'

export default function CodeBlock({ code, live, language, version, component }) {
  const [collapse, setCollapse] = useState(false)
  const [copy, setCopy] = useState(false)
  const btn = useRef(null)

  return (
    <>
      <div className={
        `${collapse ? '' : 'border-0 border-b border-solid border-slate-200'}
       box-content h-[40px] flex items-center justify-between pl-4 bg-gray-100 select-none`
      }
      >
        <div className="text-xs text-gray-500">
          <span
            onClick={() => {
              setCollapse(!collapse)
            }}
            className="cursor-pointer"
          >
            <RightOutlined rotate={collapse ? 0 : 90} className="code-editor-anchor" />
            <span className="ml-1">{live ? '编辑代码' : '查看代码'}</span>
          </span>
          <span className="mx-2">
            {
              live
                ? (
                  <Tag
                    className="cursor-pointer"
                    color="processing"
                    ref={btn}
                    onClick={() => {
                      const textarea = btn.current.closest('.code-block')?.querySelector('textarea')
                      if (!collapse) {
                        textarea?.focus()
                      } else {
                        setCollapse(false)
                        setTimeout(() => {
                          textarea?.focus()
                        }, 100)
                      }
                    }}
                  >
                    可编辑
                  </Tag>
                )
                : <Tag color="default">只读</Tag>
            }

            {
              language
                ? <Tag color="cyan">{language}</Tag>
                : undefined
            }
            {
              version
                ? <Tag color="magenta">{version}</Tag>
                : undefined
            }
          </span>
        </div>
        <div className="flex items-center h-full">
          {collapse
            ? undefined
            : (
              <div
                className={`cursor-pointer mr-2 text-opacity-60 hover:text-opacity-80 ${copy ? 'text-green-600 font-medium' : ''}`}
                onMouseLeave={() => {
                  if (copy) {
                    setTimeout(() => {
                      setCopy(false)
                    }, 200)
                  }
                }}
              >
                <CopyToClipboard text={code} onCopy={() => { setCopy(true) }}>
                  <Tooltip title={
                    copy
                      ? (
                        <>
                          <SmileOutlined />
                          <span className="ml-1">复制成功</span>
                        </>
                      )
                      : '复制代码'
                  }
                  >
                    {copy ? <CheckOutlined /> : <CopyOutlined />}
                  </Tooltip>
                </CopyToClipboard>
              </div>
            )}
        </div>
      </div>
      <div className={`${collapse ? 'hidden' : ''}`}>
        {component}
      </div>
    </>
  )
}
