import React from 'react'
import { LiveProvider, LiveEditor, LiveError, LivePreview } from 'react-live'
import theme from 'prism-react-renderer/themes/vsLight'
import CodeBlock from './CodeBlock'
import './index.css'

export default function Live({ scope, code, language, version }) {
  return (
    <LiveProvider
      code={code}
      transformCode={(source) => source}
      scope={{
        ...scope
      }}
      noInline={false}
      theme={theme}
    >
      <LivePreview className="border-0 border-b border-solid border-slate-200 py-2 px-4 min-h-[50px]" />
      <CodeBlock
        code={code}
        live
        language={language}
        version={version}
        component={<LiveEditor className="code-editor" />}
      />
      <LiveError className="bg-red-500 text-white mb-0 pt-2 px-4" />
    </LiveProvider>
  )
}
