import React from 'react'
import { LiveProvider, LiveError, LivePreview } from 'react-live'
import { mdx } from '@mdx-js/react'

export default function Preview({ scope, code }) {
  return (
    <LiveProvider
      code={code}
      scope={{
        mdx,
        ...scope
      }}
      noInline={false}
    >
      <LivePreview className="border-0 border-b border-solid border-slate-200 py-2 px-4 min-h-[50px]" />
      <LiveError className="bg-red-500 text-white mb-0 pt-2 px-4" />
    </LiveProvider>
  )
}
