export function hashchange(e, text) {
  e.preventDefault()
  window.history.pushState(null, null, `#${text}`)
  const element = document.getElementById(text)

  scrollIntoView(element)
}

export function scrollIntoView(element) {
  if (element) {
    const height = document.getElementById('navigation').offsetHeight + 10
    const top = window.pageYOffset + element.getBoundingClientRect().top - height
    window.scroll({
      top,
      behavior: 'smooth'
    })
  }
}

export function getHeadingId(text, cache) {
  let id = text
  if (cache[text]) {
    id = `${text}_${cache[text]}`
    cache[text] += 1
  } else {
    cache[text] = 1
  }
  return id
}
